.product-modal {
  width: 800px;
  max-height: 520px;
  display: flex;
  z-index: 100;
  opacity: 1;
  &__photo {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 100;
    margin: auto;
    margin-right: 20px;
    img {
      margin: auto;
      width: 100%;
      height: auto;
      max-width: 350px;
      max-height: 450px;
      z-index: 100;
    }
    &_simple {
      width: unset;
      margin-right: auto;
      img {
        max-width: 300px;
      }
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    width: 50%;
    h2,
    h3 {
      margin: 0 0 5px 0;
    }
    &-block {
      height: 100%;
      overflow-y: auto;
    }
    &-ingredients {
      display: flex;
      button {
        padding: 20px;
        border-radius: 12px;
        width: 80px;
        height: auto;
      }
      img,
      svg {
        width: -webkit-fill-available;
        height: auto;
      }
    }
    &-ingredient {
      display: flex;
      flex-direction: column;
      margin: 5px;
      max-width: 95px;
      span {
        display: block;
        text-align: center;
        font-size: 18px;
      }
      &-name {
        margin: 5px 0 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      &-price {
        font-weight: 600;
        color: var(--base-color);
      }
    }
    &-dough {
      width: 100%;
      flex-direction: column;
      > div {
        width: 100%;
        margin: 10px 0;
      }
      button {
        max-width: 100%;
        flex: 1;
        justify-content: center;
        padding: 7px 20px;
        font-size: 18px;
      }
    }
    &-total {
      display: flex;
      align-items: center;
      margin-top: 20px;
      h2 {
        font-size: 24px;
        color: var(--base-color);
        margin: 0;
      }
      span {
        font-size: 18px;
        color: var(--text-secondary);
        margin: 7px 0 0 10px;
      }
      button {
        margin-left: auto;
        padding: 7px 17px;
        font-size: 18px;
      }
      &_simple {
        h2 {
          font-size: 28px;
        }
        button {
          padding: 10px 25px;
        }
      }
    }
    &_simple {
      width: unset;
      > h2 {
        text-align: center;
        font-size: 35px;
        margin-top: 15px;
      }
      > span {
        width: 100%;
      }
    }
  }
  &_simple {
    flex-direction: column;
    width: 400px;
  }
}
